.ecl-icon{fill:currentcolor;margin:0;transition:transform .3s ease-in-out}.ecl-icon--2xs{height:.75rem;width:.75rem}.ecl-icon--xs{height:1rem;width:1rem}.ecl-icon--s{height:1.25rem;width:1.25rem}.ecl-icon--m{height:1.5rem;width:1.5rem}.ecl-icon--l{height:2rem;width:2rem}.ecl-icon--xl{height:2.5rem;width:2.5rem}.ecl-icon--2xl{height:3rem;width:3rem}.ecl-icon--fluid{height:1em;width:1em}.ecl-icon--primary{fill:#004494}.ecl-icon--inverted{fill:#fff}.ecl-icon--rotate-90{transform:rotate(90deg)}.ecl-icon--rotate-180{transform:rotate(180deg)}.ecl-icon--rotate-270{transform:rotate(270deg)}.ecl-icon--flip-horizontal{transform:scaleX(-1)}.ecl-icon--flip-vertical{transform:scaleY(-1)}